import { useQuery } from "@tanstack/react-query";
import { Suspense } from "react";
import { getImageUrl } from "../../app/api";

const imagePromise = (src) => () => new Promise((resolve) => /*setTimeout(() => */ {
    const img = new Image();
    img.onload = () => {
        resolve(img);
    }
    img.src = `${src}`
}/*, 2000)*/);

export default function LazyImg({ src, alt, defaultImg = undefined, ...props }) {
    return (
        <Suspense fallback={<img src={getImageUrl({ id: undefined, img: undefined }, defaultImg)} alt={alt} {...props} />}>
            <Img src={src} alt={alt} {...props} />
        </Suspense>
    );
}

function Img({ src, alt, ...props }) {
    const { data: imgObject } = useQuery(
        [src],
        imagePromise(src),
        { suspense: true }
    );

    return (<img src={imgObject.src} alt={alt} {...props} />)
}