import StarIcon from '@mui/icons-material/Star';
import { Rating, Typography, Unstable_Grid2 as Grid } from '@mui/material';
import { useSelector } from 'react-redux';
import { selectAllLangues } from '../../app/redux/languesSlice';

export default function Langues() {

    const langues = useSelector(selectAllLangues);

    return (
        <Grid container spacing={{ xs: 1, sm: 1, md: 2 }} columns={{ xs: 6, sm: 6, md: 6 }}>
            {
                langues.map((langue, i) =>
                    <Grid xs={3} sm={3} md={3} key={`grid-${i}`}>
                        <Typography component="legend">{langue.label}</Typography>
                        <span style={{ display: 'flex' }}>
                            <Rating
                                name={langue.label}
                                value={langue.note}
                                readOnly
                                precision={1}
                                emptyIcon={<StarIcon style={{ opacity: 0.55 }} fontSize="inherit" />}
                            />&nbsp;
                            <span style={{ fontStyle: 'italic', opacity: 0.7 }}>
                                : {langue.niveau}
                            </span>
                        </span>
                    </Grid>
                )
            }
        </Grid>
    )
}