import { createEntityAdapter, createSlice } from "@reduxjs/toolkit";
import getData from "../api";

const interets = getData('centresInteret')

const interetsAdapter = createEntityAdapter(
    {
        selectId: (interet) => interet.id,
        sortComparer: (i1, i2) => i1.id - i2.id
    }
)

const initialState = interetsAdapter.addMany(
    interetsAdapter.getInitialState({
        status: 'succeeded',
        error: null
    }),
    interets
);

const interetsSlice = createSlice({
    name: 'interets',
    initialState: initialState,
    reducers: {}
})

export const { selectAll: selectAllInterets, selectById: selectInteretById } = interetsAdapter.getSelectors(state => state.interets);

export default interetsSlice.reducer;