import { createEntityAdapter, createSlice } from "@reduxjs/toolkit";
import getData from "../api";

const competences = getData('competences');

const competenceAdapter = createEntityAdapter({
    selectId: (competence) => competence.id,
    sortComparer: (c1, c2) => {
        return c1.id - c2.id
    }
})

const initialState = competenceAdapter.addMany(
    competenceAdapter.getInitialState({
        status: 'succeeded',
        error: null
    }),
    competences
);

const competenceSlice = createSlice({
    name: 'competences',
    initialState: initialState,
    reducers: {

    }
})

export const {
    selectAll: selectAllCompetences,
    selectById: selectCompetenceById
} = competenceAdapter.getSelectors(state => state.competences)

export default competenceSlice.reducer;