import { configureStore } from '@reduxjs/toolkit';
import accordeons from './accordeonsSlice';
import competences from './competencesSlice';
import contacts from './contactSlice';
import etudes from './etudesSlice';
import experiences from './experiencesSlice';
import interets from './interetsSlice';
import langues from './languesSlice';

export const store = configureStore({
  reducer: {
    contacts: contacts,
    competences: competences,
    accordeons: accordeons,
    experiences: experiences,
    etudes: etudes,
    langues: langues,
    interets: interets
  },
});
