import { createEntityAdapter } from "@reduxjs/toolkit";
import { createSlice } from "@reduxjs/toolkit";
import getData from "../api";

const langues = getData('langues')

const languesAdapter = createEntityAdapter(
    {
        selectId: (langue) => langue.id,
        sortComparer: (l1, l2) => l1.id - l2.id
    }
)

const initialState = languesAdapter.addMany(
    languesAdapter.getInitialState({
        status: 'succeeded',
        error: null
    }),
    langues
);

const languesSlice = createSlice({
    name: 'langues',
    initialState: initialState,
    reducers: {}
})

export const { selectAll: selectAllLangues, selectById: selectLangueById } = languesAdapter.getSelectors(state => state.langues);

export default languesSlice.reducer;