import { useTheme } from "@mui/system";
import { useQuery } from "@tanstack/react-query";
import { Suspense } from "react";
import { getImageUrl } from "../../app/api";
import styles from "./CompetenceIcon.module.css";

const imagePromise = (src) => () => new Promise((resolve) => /*setTimeout(() => */ {
    const img = new Image();
    img.onload = () => {
        resolve(img);
    }
    img.src = `${src}`
}/*, 2000)*/);

function isDarkMode(theme) {
    return theme.palette.mode === 'dark';
}

const style = (theme) => {
    return {
        border: `0.1em solid ${theme.palette.primary.main}`,
        boxShadow: `0.1em 0.1em 0.3em 0.01em ${isDarkMode(theme) ? 'black' : 'grey'}`
    }
}


export default function CompetenceIcon({ competence, ...props }) {
    const theme = useTheme();
    return (
        <Suspense fallback={<img className={styles['competence-icon']} src={getImageUrl({id: undefined, img: undefined})} alt={`${competence.label}`} style={style(theme)} />}>
            <Img src={getImageUrl(competence)} alt={`${competence.label}`} {...props} />
        </Suspense>
    );
}

function Img({ src, alt, ...props }) {
    const theme = useTheme();
    const { data: imgObject } = useQuery(
        [src],
        imagePromise(src),
        { suspense: true }
    );

    return (<img className={styles['competence-icon']} src={imgObject.src} alt={alt} {...props} style={style(theme)} />)
}