import { Paper } from '@mui/material'
import styles from './Experience.module.css'
import BusinessRoundedIcon from '@mui/icons-material/BusinessRounded';
import CheckCircleOutlineRoundedIcon from '@mui/icons-material/CheckCircleOutlineRounded';
import { Fragment } from 'react';
import { useTheme } from '@mui/material';

export default function Experience({ experience }) {
    const theme = useTheme();

    return (
        <div className={styles.experience} key={`topdiv-mission-exp-${experience.id}`}>
            <Paper elevation={4} className={`${styles.paper} ${styles.bloc}`} sx={{ wordBreak: "break-word", display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'flex-start' }} key={`paper-mission-exp-${experience.id}`}>
                <span className={`${styles.item} ${styles.entreprise}`} key={`entreprise-mission-exp-${experience.id}`}><BusinessRoundedIcon /> {experience.entreprises.map(entreprise => entreprise.label).join(' - ')}</span>
                <span className={`${styles.item} ${styles.role}`} key={`role-mission-exp-${experience.id}`}>{experience.roles.map(role => role.label).join(' / ')}</span>
                <span style={{ paddingTop: '0.5em' }} key={`span-mission-exp-${experience.id}`}>Missions :</span>
                <div className={styles.missions} key={`div-mission-exp-${experience.id}`}>
                    {experience.missions.map((mission, index) =>
                        <Fragment>
                            <span className={`${styles.item} ${styles.mission}`} key={`span-exp-${experience.id}-mission-${index}`}>
                                <CheckCircleOutlineRoundedIcon /> {mission.description}
                            </span>
                            {mission.technos.length > 0 &&
                                <div className={styles.technos} key={`div-exp-${experience.id}-mission-${index}`}>
                                    {mission.technos.map((techno, indexTechno) =>
                                        <span className={`${styles.techno}`} style={{ color: indexTechno % 2 === 0 ? theme.palette.primary.main : theme.palette.secondary.main }} key={`exp-${experience.id}-mission-${index}-tech-${indexTechno}`}>
                                            {techno.label}
                                        </span>
                                    )}
                                </div>
                            }
                        </Fragment>
                    )}
                </div>
            </Paper>
        </div>
    )
}