import { styled, Typography } from "@mui/material";

const Blok = styled(Typography)(({ theme }) => ({
    display: 'inline',
    fontSize: 'inherit',
    fontWeight: 'bold',
    color: theme.palette.primary.main
}));

export default function EmphasisPrimaryTypography({ children, ...props }) {
    return (
        <Blok {...props}>
            {children}
        </Blok>
    )
}
