import MenuIcon from '@mui/icons-material/Menu';
import { AppBar, IconButton, Toolbar, Tooltip } from "@mui/material";
import logo from '../../logo.svg';
import { ThemeToggleButton } from "../../ThemeSetter";

export default function Header({ appBarHeight, handleDrawerToggle }) {

    const sizeMenuButton = '1.5em'

    function isDarkMode(theme) {
        return theme.palette.mode === 'dark';
    }

    return (
        <AppBar
            position="fixed" color="primary" enableColorOnDark
            sx={{
                zIndex: (theme) => theme.zIndex.drawer + 1,
                height: appBarHeight
            }}
        >
            <Toolbar>
                <IconButton
                    color="inherit"
                    aria-label="open drawer"
                    edge="start"
                    onClick={handleDrawerToggle}
                    sx={{ mr: 2, display: { sm: 'none' }, minWidth: sizeMenuButton, minHeight: sizeMenuButton, boxShadow: (theme) => `0.1em 0.1em 0.3em 0.01em ${isDarkMode(theme) ? 'black' : 'purple'}` }}
                >
                    <MenuIcon sx={{ minWidth: sizeMenuButton, minHeight: sizeMenuButton }} />
                </IconButton>
                <ThemeToggleButton />
                <Tooltip title={'Powered by React x Redux'}>
                    <span className='mini-logo-span'>
                        <img className="mini-logo" src={logo} alt="logo" />
                    </span>
                </Tooltip>
            </Toolbar>
        </AppBar>
    )
}