import { createEntityAdapter, createSlice } from "@reduxjs/toolkit";
import getData from "../api";

const etudes = getData('etudes')

const etudesAdapter = createEntityAdapter(
    {
        selectId: (etude) => etude.id,
        sortComparer: (e1, e2) => e1.id - e2.id
    }
)

const initialState = etudesAdapter.addMany(
    etudesAdapter.getInitialState({
        status: 'succeeded',
        error: null
    }),
    etudes
);

const etudesSlice = createSlice({
    name: 'etudes',
    initialState: initialState,
    reducers: {}
})

export const { selectAll: selectAllEtudes, selectById: selectEtudeById } = etudesAdapter.getSelectors(state => state.etudes);

export default etudesSlice.reducer;