import { firstValueFrom, of } from 'rxjs';
import defaultDefaultImg from '../data/default.png';
import data from '../data/static.json';

const { serverUrl, contact, typesCompetence, competences, experiencesPro, etudes, langues, formations, centresInteret } = data;

export async function getDataPromise(url) {
    return firstValueFrom(of(getData(url)));
}

export default function getData(url) {
    if (url === 'contacts') {
        return [contact];
    }
    if (url === 'contact') {
        return contact;
    }
    if (url === 'typesCompetence') {
        return typesCompetence;
    }
    if (url === 'competences') {
        return competences;
    }
    if (url === 'experiencesPro') {
        return experiencesPro;
    }
    if (url === 'etudes') {
        return etudes;
    }
    if (url === 'langues') {
        return langues;
    }
    if (url === 'formations') {
        return formations;
    }
    if (url === 'centresInteret') {
        return centresInteret;
    }
    if (url === 'serverUrl') {
        return serverUrl;
    }
    throw Error("On ne peut rien fetcher de l'url : " + url);
}

export function getImageUrl({ id, img }, defaultImg = undefined) {
    if (exists(id) && exists(img)) {
        return `${serverUrl}/${img}/${id}.png`;
    }
    if (exists(defaultImg)) {
        return defaultImg
    }
    return defaultDefaultImg
}

function exists(variable) {
    return variable !== null && variable !== undefined
}