import { useSelector } from "react-redux";
import { selectAllCompetences } from "../../app/redux/competencesSlice";
import NestedList from "./NestedList";

export default function Competence() {
    const allCompetences = useSelector(state => selectAllCompetences(state))

    return (
            <NestedList competences={allCompetences}></NestedList>
    )
}