import Timeline from '@mui/lab/Timeline';
import TimelineConnector from '@mui/lab/TimelineConnector';
import TimelineContent from '@mui/lab/TimelineContent';
import TimelineDot from '@mui/lab/TimelineDot';
import TimelineItem from '@mui/lab/TimelineItem';
import TimelineOppositeContent from '@mui/lab/TimelineOppositeContent';
import TimelineSeparator from '@mui/lab/TimelineSeparator';
import { useSelector } from 'react-redux';
import { selectAllEtudes } from '../../app/redux/etudesSlice';
import DateLieu from '../exppro/DateLieu';
import Etude from './Etude';
import styles from './Scolarite.module.css';


export default function Scolarite() {

    const etudes = useSelector(selectAllEtudes);

    return (
        <div className={styles.etudes}>
            <Timeline
                sx={{
                    '& .MuiTimeline-root': {
                        marginBlockStart: 'unset',
                        marginBlockEnd: 'unset',
                        paddingInlineStart: 'unset'
                    },
                    '& .MuiTypography-root': {
                        '&.MuiTimelineOppositeContent-root': {
                            fontSize: 'unset',
                            padding: 'unset',
                            paddingRight: '1em',
                            maxWidth: '8em'
                        },
                        '&.MuiTimelineContent-root': {
                            fontSize: 'unset',
                            padding: 'unset',
                            paddingLeft: '1em'
                        }
                    }
                }}
            >

                {
                    etudes.map((etude, index) =>
                        <TimelineItem key={`tli-${index}`}>
                            <TimelineOppositeContent key={`tloc-${index}`} position='left' /*color="text.secondary"*/>
                                <DateLieu key={`dl-${index}`} date={`${etude.debut}  ${etude.fin}`} lieu={`${etude.lieu}`} />
                            </TimelineOppositeContent>
                            <TimelineSeparator key={`tls-${index}`}>
                                <TimelineDot key={`tld-${index}`} />
                                <TimelineConnector key={`tc-${index}`} />
                            </TimelineSeparator>
                            <TimelineContent key={`tlc-${index}`} position='right'>
                                <Etude key={`etude-${index}`} etude={etude} />
                            </TimelineContent>
                        </TimelineItem>
                    )
                }

            </Timeline>
        </div>
    );
}