import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { Accordion, AccordionDetails, AccordionSummary, styled, Typography } from '@mui/material';
import { useDispatch, useSelector } from 'react-redux';
import { selectAccordion, selectedAccordionSelector } from '../../app/redux/accordeonsSlice';
import Competence from "../competences/Competences";
import Etudes from '../etudes/Etudes';
import ExperiencesPro from '../exppro/ExperiencesPro';
import Interets from '../interets/Interets';
import Presentation from '../presentation/Presentation';
import styles from './Main.module.css';

export default function Main() {
    const elevation = 5;
    const dispatch = useDispatch();
    const selectedAccordion = useSelector(selectedAccordionSelector);

    const handleClick = (panel) => (event) => {
        dispatch(selectAccordion({ selected: panel, collapseAll: false }))
    }

    function isAccordionSelected(selectedAccordeon) {
        return selectedAccordion.selected[selectedAccordeon]
    }

    function isDarkMode(theme) {
        return theme.palette.mode === 'dark';
    }

    const BoldTypography = styled(Typography)(({ theme }) => ({
        fontWeight: 'bolder',
        fontSize: '1.5em',
        textTransform: 'uppercase',
        color: theme.palette.primary.main,
        textShadow: `0.2em 0.2em 0.2em ${isDarkMode(theme) ? '#102027' : 'lightgray'}`
    }));

    return (
        <div className={styles.accordeons}>

            {/* ===== Prsentation ===== */}
            <Accordion expanded={isAccordionSelected('presentation')} defaultExpanded={true} elevation={elevation}>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panelPresentation-content"
                    id="panelPresentation-header"
                    onClick={handleClick('presentation')}
                >
                    <BoldTypography>PRSENTATION</BoldTypography>
                </AccordionSummary>
                <AccordionDetails>
                    <Presentation />
                </AccordionDetails>
            </Accordion>

            {/* ===== Comptences ===== */}
            <Accordion expanded={isAccordionSelected('competences')} defaultExpanded={true} elevation={elevation}>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panelCompetences-content"
                    id="panelCompetences-header"
                    onClick={handleClick('competences')}
                >
                    <BoldTypography>COMPTENCES</BoldTypography>
                </AccordionSummary>
                <AccordionDetails>
                    <Competence />
                </AccordionDetails>
            </Accordion>

            {/* ===== Expriences professionnelles ===== */}
            <Accordion expanded={isAccordionSelected('experiences')} defaultExpanded={true} elevation={elevation} /*sx={{
                '& .MuiCollapse-wrapperInner': {
                    display: 'flex',
                    justifyContent: 'start'
                }
            }}*/>
                <AccordionSummary

                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panelExperiencePro-content"
                    id="panelExperiencePro-header"
                    onClick={handleClick('experiences')}
                >
                    <BoldTypography>EXPRIENCES PROFESSIONNELLES</BoldTypography>
                </AccordionSummary>
                <AccordionDetails>
                    <ExperiencesPro />
                </AccordionDetails>
            </Accordion>

            {/* ===== tudes ===== */}
            <Accordion expanded={isAccordionSelected('etudes')} defaultExpanded={true} elevation={elevation}>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panelEtudes-content"
                    id="panelEtudes-header"
                    onClick={handleClick('etudes')}
                >
                    <BoldTypography>TUDES</BoldTypography>
                </AccordionSummary>
                <AccordionDetails>
                    <Etudes />
                </AccordionDetails>
            </Accordion>

            {/* ===== Intrts ===== */}
            <Accordion expanded={isAccordionSelected('interets')} defaultExpanded={true} elevation={elevation}>
                <AccordionSummary
                    expandIcon={<ExpandMoreIcon />}
                    aria-controls="panelInterets-content"
                    id="panelInterets-header"
                    onClick={handleClick('interets')}
                >
                    <BoldTypography>CENTRES D'INTRTS</BoldTypography>
                </AccordionSummary>
                <AccordionDetails>
                    <Interets />
                </AccordionDetails>
            </Accordion>

        </div >
    )
}