import { createEntityAdapter, createSlice } from "@reduxjs/toolkit";
import getData from "../api";

const contacts = getData('contacts');

const contactAdapter = createEntityAdapter({
    selectId: (contact) => contact.mail,
    sortComparer: (c1, c2) => {
        return c1.nom.localCompare(c2.nom)
    }
})

const initialState = contactAdapter.addMany(
    contactAdapter.getInitialState({
        status: 'succeeded',
        error: null,
        moi: contacts[0]
    }),
    contacts
);

const contactSlice = createSlice({
    name: 'contacts',
    initialState: initialState,
    reducers: {

    }
})

export const {
    selectAll: selectAllContacts,
    selectById: selectContactById
} = contactAdapter.getSelectors(state => state.contacts)

export default contactSlice.reducer;