import BadgeOutlinedIcon from '@mui/icons-material/BadgeOutlined';
import EmailOutlinedIcon from '@mui/icons-material/EmailOutlined';
import HomeOutlinedIcon from '@mui/icons-material/HomeOutlined';
import LocalPhoneOutlinedIcon from '@mui/icons-material/LocalPhoneOutlined';
import { Divider, Paper, useTheme } from "@mui/material";
import { useSelector } from "react-redux";
import styles from './Contact.module.css';

export default function Contact() {
    const moi = useSelector(state => state.contacts.moi);
    const theme = useTheme();
    //const age = parseInt(Math.floor(DateTime.local().diff(DateTime.local(1981, 6, 29), 'years').years).toFixed(0), 10);

    function isDarkMode(theme) {
        return theme.palette.mode === 'dark';
    }

    return (
        <Paper elevation={8} sx={{ wordBreak: "break-word", display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
            <div className={styles.contact} style={{
                textShadow: `0.2em 0.2em 0.1em ${isDarkMode(theme) ? '#102027' : 'lightgray'}`,
                color: theme.palette.primary.main
            }}>
                Contact
            </div>
            <Divider className={`${styles.muiDivideRoot} ${styles.customDivide}`} sx={{ width: '50%' }} />
            {
                <div className={styles.infos}>
                    <div className={styles.info}>
                        <BadgeOutlinedIcon />{moi.prenom} {moi.nom}
                    </div>
                    <div className={styles.info}>
                        <HomeOutlinedIcon />{moi.adresse}
                    </div>
                    <div className={styles.info}>
                        <EmailOutlinedIcon />{moi.mail}
                    </div>
                    {/*<div className={styles.info}>
                        <EventOutlinedIcon />{age} ans
                    </div>*/}
                    <div className={styles.info}>
                        <LocalPhoneOutlinedIcon />{moi.telephone}
                    </div>
                </div>
            }
        </Paper>
    )
}