import { Fade, Tab, Tabs } from "@mui/material";
import PropTypes from 'prop-types';
import { Fragment, useState } from "react";
import Formations from "../formations/Formations";
import Langues from "./Langues";
import Scolarite from "./Scolarite";

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Fragment>{children}</Fragment>
            )}
        </div>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.number.isRequired,
    value: PropTypes.number.isRequired,
};

function a11yProps(index) {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`,
    };
}

export default function Etudes() {
    const [value, setValue] = useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <div className="tabs">
            <Tabs value={value} onChange={handleChange} aria-label="tudes, formations et langues">
                <Tab label="tudes" {...a11yProps(0)} />
                <Tab label="Formations" {...a11yProps(1)} />
                <Tab label="Langues" {...a11yProps(2)} />
            </Tabs>


            <TabPanel value={value} index={0} aria-label="tudes">
                <Fade in={value === 0}>
                    <div>
                        <Scolarite />
                    </div>
                </Fade>
            </TabPanel>
            <TabPanel value={value} index={1} aria-label="Formations">
                <Fade in={value === 1}>
                    <div>
                        <Formations />
                    </div>
                </Fade>
            </TabPanel>
            <TabPanel value={value} index={2} aria-label="Langues">
                <Fade in={value === 2}>
                    <div style={{maxWidth: 'max-content', paddingTop: '2em', paddingBottom: '1.5em', paddingLeft: '1em'}}>
                        <Langues />
                    </div>
                </Fade>
            </TabPanel>
        </div>
    );
}