import DeveloperBoardTwoToneIcon from '@mui/icons-material/DeveloperBoardTwoTone';
import ExpandLess from '@mui/icons-material/ExpandLess';
import ExpandMore from '@mui/icons-material/ExpandMore';
import { Unstable_Grid2 as Grid } from '@mui/material';
import Collapse from '@mui/material/Collapse';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import { useState } from 'react';
import getData from '../../app/api';
import EmphasisPrimaryTypography from '../UI/EmphasisPrimaryTypography';
import CompetenceIcon from './CompetenceIcon';
import styles from './NestedList.module.css';

const typesCompetence = getData('typesCompetence')

export default function NestedList({ competences }) {
    const [open, setOpen] = useState(typesCompetence.map(c => true));

    const handleClick = (category) => () => {
        const newOpen = open.concat([]);
        newOpen[category] = !newOpen[category]
        setOpen(newOpen);
    };

    const groupByTypeCompetence = competences.reduce((all, currentComp) => {
        if (!all[currentComp.typeCompetence]) {
            all[currentComp.typeCompetence] = []
        }

        all[currentComp.typeCompetence].push(currentComp);

        return all
    }, {});

    return (
        <List
            sx={{ width: '100%', maxWidth: '60em' }}
            component="nav"
            aria-labelledby="nested-list-subheader"
        >
            <Grid container spacing={{ xs: 0, sm: 1, md: 2}} columns={{ xs: 2, sm: 6, md: 12 }}>
                {
                    Object.keys(groupByTypeCompetence).map((cle, i) => {
                        return (
                            <Grid xs={2} sm={3} md={4} key={i}>
                                <ListItemButton key={`lib-sup${i}`} onClick={handleClick(i)} className={`${styles.toplevel} ${styles.item}`}>
                                    <ListItemIcon key={`lic-sup${i}`}>
                                        <DeveloperBoardTwoToneIcon key={`dbtti${i}`} />
                                    </ListItemIcon>
                                    <ListItemText key={`lit-sup${i}`} primary={<EmphasisPrimaryTypography className={`${styles.categorie} ${styles.item}`}>{`${typesCompetence[cle].label}`}</EmphasisPrimaryTypography>} />
                                    {open[i] ? <ExpandLess key={`exl-sup${i}`} /> : <ExpandMore key={`exm-sup${i}`} />}
                                </ListItemButton>
                                <Collapse key={`collapse-sup${i}`} in={open[i]} timeout="auto" unmountOnExit>
                                    <List key={`list-sup${i}`} component="div" disablePadding>
                                        {
                                            groupByTypeCompetence[cle].map((comp, j) =>
                                            (
                                                <ListItemButton key={`lib${j}`} sx={{ pl: 6 }} className={`${styles.sublevel} ${styles.item}`}>
                                                    <ListItemIcon key={`lii${j}`}>
                                                        <CompetenceIcon key={`ci${j}`} competence={comp} />
                                                    </ListItemIcon>
                                                    <ListItemText key={`lit${j}`} primary={<span className={`${styles.competence} ${styles.item}`}>{`${comp.label}`}</span>} />
                                                </ListItemButton>
                                            ))
                                        }
                                    </List>
                                </Collapse>
                            </Grid>
                        )
                    }
                    )
                }
            </Grid>
        </List>
    );
}
