import { Drawer, useMediaQuery, useTheme } from '@mui/material';
import { useEffect, useRef, useState } from 'react';
import './App.css';
import getData from './app/api';
import Header from './components/header/Header';
import Main from './components/main/Main';
import SideBar from './components/sidebar/SideBar';
import SimpleBackdrop from './components/UI/SimpleBackdrop';


const drawerWidth = '20%';
const appBarHeight = '64px';
const contact = getData('contact');

function App() {
  const printRef = useRef();
  const [mobileOpen, setMobileOpen] = useState(false);
  const [backDropOpen, setBackDropOpen] = useState(false);
  const matches = useMediaQuery('(min-width:600px)');
  const theme = useTheme();

  function isDarkMode(theme) {
    return theme.palette.mode === 'dark';
  }

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const handleDownloadPng = async () => {
    setBackDropOpen(true)
    /*const element = printRef.current;
    const canvas = await html2canvas(element);
    const data = canvas.toDataURL('image/png');
    */
    const link = document.createElement('a');
    const serverUrl = getData('serverUrl')
    if (typeof link.download === 'string') {
      link.href = serverUrl + '/img/cv/CV.pdf';
      link.download = `CV_${contact.mail}.pdf`;

      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    }/* else {
      window.open(data);
    }*/
    setBackDropOpen(false)
  };

  useEffect(() => {
    function handleResize() {
      if (!matches) {
        setMobileOpen(false)
      }
    }

    window.addEventListener('resize', handleResize)

    return () => window.removeEventListener('resize', handleResize);
  }, [matches, setMobileOpen])

  return (
    <div className="App">
      <SimpleBackdrop open={backDropOpen} />
      <header className="App-header">
        <Header appBarHeight={appBarHeight} handleDrawerToggle={handleDrawerToggle} />
      </header>
      <main ref={printRef} className="App-main" style={{ marginLeft: (matches ? drawerWidth : 0), marginTop: appBarHeight, width: `${matches ? 'calc(100% - ' + drawerWidth + ')' : '100%'}` }}>
        <Drawer
          variant={matches ? 'permanent' : 'temporary'}
          open={mobileOpen}
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
            hideBackdrop: matches
          }}
          onClose={handleDrawerToggle}
          PaperProps={{
            sx: {
              width: (matches ? drawerWidth : '45%'),
              boxShadow: `${matches ? '0.1em 0.1em 0.3em 0.01em ' + (isDarkMode(theme) ? 'black' : 'black') : 'none'}`,
              backgroundColor: '#11ffee20'
            }
          }}
        >
          <div style={{ marginTop: appBarHeight }}>
            <SideBar handleDownloadPng={handleDownloadPng} />
          </div>
        </Drawer>
        <Main />
      </main>
    </div>
  );
}

export default App;
