import { Unstable_Grid2 as Grid } from '@mui/material';
import ImageListItemBar from '@mui/material/ImageListItemBar';
import { useSelector } from 'react-redux';
import { getImageUrl } from '../../app/api';
import { selectAllInterets } from '../../app/redux/interetsSlice';
import LazyImg from '../UI/LazyImg';
import defaultImg from '../../data/default-interet.png'; 

const height = '9em'
const width = '12em'
const borderRadius = '2em'
const titleHeight = '2em'
const dimensionsStyle = { height: height, width: width, borderRadius: borderRadius }

export default function Interets() {
    const interets = useSelector(selectAllInterets);

    return (
        <Grid container spacing={{ xs: 2, sm: 2, md: 2 }} columns={{ xs: 2, sm: 4, md: 12 }} sx={{ maxWidth: '90vmin' }}>
            {
                interets.map((interet, i) => (
                    <Grid xs={1} sm={2} md={3} key={`div${i}`}>
                        <div style={{ position: 'relative', pointerEvents: 'none', ...dimensionsStyle }}>
                            <LazyImg src={getImageUrl(interet)} alt={interet.label} defaultImg={defaultImg} style={dimensionsStyle} />
                            <ImageListItemBar key={i} sx={{ position: 'absolute', top: `calc(${height} - ${titleHeight})`, borderRadius: `0 0 ${borderRadius} ${borderRadius}`, textAlign: 'center', width: width }} title={interet.label} />
                        </div>
                    </Grid>
                ))
            }
        </Grid>
    );
}
