import { Button } from "@mui/material";
import { useDispatch } from "react-redux";
import { selectAccordion } from "../../app/redux/accordeonsSlice";
import Contact from '../contact/Contact.js';
import styles from './SideBar.module.css';

export default function SideBar({ handleDownloadPng, ...props }) {
    const dispatch = useDispatch();

    function actionPrint() {
        dispatch(selectAccordion({ selected: 'none', expandAll: true }))
        setTimeout(() => handleDownloadPng(), 500);
    }

    return (
        <div
            style={{ display: 'flex', flexDirection: 'column', gap: '1em', margin: '0.6em', wordWrap: "break-word" }}
            className={styles.sidebar}
        >

            <Contact />

            <Button variant="contained" onClick={() => dispatch(selectAccordion({ selected: 'presentation', collapseAll: true }))}>Prsentation</Button>
            <Button variant="contained" onClick={() => dispatch(selectAccordion({ selected: 'competences', collapseAll: true }))}>Comptences</Button>
            <Button variant="contained" onClick={() => dispatch(selectAccordion({ selected: 'experiences', collapseAll: true }))}>Expriences professionnelles</Button>
            <Button variant="contained" onClick={() => dispatch(selectAccordion({ selected: 'etudes', collapseAll: true }))}>tudes</Button>
            <Button variant="contained" onClick={() => dispatch(selectAccordion({ selected: 'interets', collapseAll: true }))}>Centres d'intrts</Button>

            <Button variant="contained" color="secondary" onClick={() => actionPrint()} sx={{ marginTop: '5em' }}>Tlcharger le CV</Button>
        </div>
    )
}