import { createEntityAdapter, createSlice } from "@reduxjs/toolkit";
import getData from "../api";

const experiences = getData('experiencesPro');

const experienceAdapter = createEntityAdapter({
    selectId: (experience) => experience.id,
    sortComparer: (c1, c2) => {
        return c1.id - c2.id
    }
})

const initialState = experienceAdapter.addMany(
    experienceAdapter.getInitialState({
        status: 'succeeded',
        error: null
    }),
    experiences
);

const experienceSlice = createSlice({
    name: 'experiences',
    initialState: initialState,
    reducers: {

    }
})

export const {
    selectAll: selectAllExperiences,
    selectById: selectExperienceById
} = experienceAdapter.getSelectors(state => state.experiences)

export default experienceSlice.reducer;