import styles from './DateLieu.module.css'
import DateRangeTwoToneIcon from '@mui/icons-material/DateRangeTwoTone';
import PlaceTwoToneIcon from '@mui/icons-material/PlaceTwoTone';

export default function DateLieu({ date, lieu }) {
    return (
        <div className={styles.datelieu}>
            {date && <div className={styles.date}><DateRangeTwoToneIcon /> {date}</div>}
            {lieu && <div className={styles.lieu}><PlaceTwoToneIcon /> {lieu}</div>}
        </div>
    )
}