import { createSlice } from "@reduxjs/toolkit";

const accordeonSlice = createSlice({
    name: 'accordeons',
    initialState: {
        selected: {
            presentation: true,
            competences: true,
            experiences: true,
            etudes: true,
            interets: true,
            none: true
        },
        collapseAll: false,
        expandAll: false
    },
    reducers: {
        selectAccordion(state, action) {
            state.collapseAll = action.payload.collapseAll
            if (action.payload.collapseAll) {
                state.selected = {
                    ...state,
                    presentation: false,
                    competences: false,
                    experiences: false,
                    etudes: false,
                    interets: false
                }
            } else if (action.payload.expandAll) {
                state.selected = {
                    ...state,
                    presentation: true,
                    competences: true,
                    experiences: true,
                    etudes: true,
                    interets: true
                }
            }
            state.selected[action.payload.selected] = !state.selected[action.payload.selected]
        }
    }
})

export const selectedAccordionSelector = (state) => state.accordeons;
export const { selectAccordion } = accordeonSlice.actions;
export default accordeonSlice.reducer;